import { createStore } from "vuex";

export default createStore({
  state: {
    count: 0,
    activeMenu: "/home",
  },
  mutations: {
    devChangeData(state, value) {
      state.count = value;
    },
    changeActiveMenu(state, value) {
      state.activeMenu = value;
    },
  },
  getters: {
    getCount(state) {
      return state.count;
    },
    getActiveMenu(state) {
      return state.activeMenu;
    },
  },
  actions: {
    increaseCount(context) {
      context.commit("devChangeData", this.getters.getCount + 1);
    },
    decreaseCount(context) {
      context.commit("devChangeData", this.getters.getCount - 1);
    },
    changeMenu(context, menu) {
      context.commit("changeActiveMenu", menu);
    },
  },
  modules: {},
});
