<template>
  <div
    class="min-h-screen flex items-center justify-center bg-gray-50 py-12 px-4 sm:px-6 lg:px-8"
  >
    <div class="max-w-md w-full space-y-8">
      <div>
        <img class="mx-auto h-24 w-auto" src="../assets/images/pharmacy.png" />
        <h2 class="mt-6 text-center text-2xl font-extrabold text-gray-900">
          <router-link :to="{ name: 'Home' }">ร้านขายยากฤติยาเภสัช</router-link>
        </h2>
      </div>
      <form class="mt-8 space-y-6" @submit.prevent="handleSubmit">
        <div class="rounded-md shadow-sm -space-y-px">
          <BaseInput type="text" v-model="username" placeholder="ชื่อผู้ใช้" />
          <div class="pt-4"></div>
          <BaseInput
            type="password"
            v-model="password"
            placeholder="รหัสผ่าน"
          />
        </div>
        <div>
          <AppButton color="bg-red-400" text="เข้าสู่ระบบ" />
        </div>
      </form>

      <AlertDialog
        v-if="showAlert"
        @closePressed="close"
        erroMessage="กรุณาตรวจสอบ ชื่อผู้ใช้งาน และ รหัสผ่าน"
        color="bg-purple-800"
      />
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import AppButton from "../components/Button";
import BaseInput from "../components/BaseInput.vue";

import AlertDialog from "../components/AlertDialog";
import router from "@/router";
export default {
  components: { AppButton, BaseInput, AlertDialog },
  setup() {
    const username = ref("");
    const password = ref("");
    const showAlert = ref(false);

    const handleSubmit = () => {
      console.log(username.value);
      console.log(password.value);
      if (username.value == "1" && password.value == "1") {
        router.push({ name: "Home" });
      } else {
        showAlert.value = true;
      }
    };

    const close = () => {
      showAlert.value = false;
      username.value = "";
      password.value = "";
    };

    return { username, password, showAlert, handleSubmit, close };
  },
};
</script>

