<template>
  <NavBar />
  <div class="py-8 px-10">
    <div class="md:grid md:grid-cols-5 md:gap-6">
      <div class="md:col-span-1">
        <div class="px-4 sm:px-0">
          <h3 class="text-lg font-medium leading-6 text-gray-900">ระสมาชิก</h3>
          <p class="mt-1 text-sm text-gray-600">
            เพิ่มสมาชิกเข้าระบบ
          </p>
        </div>
      </div>
      <div class="md:col-span-4">
        <form class="px-4 sm:px-0" @submit.prevent="handleSubmit">
          <div class="pt-4">
            <BaseInput type="text" v-model="customerName" placeholder="ชื่อ" />
          </div>
          <div class="pt-4">
            <BaseInput
              type="text"
              v-model="customerLastName"
              placeholder="นามสกุล"
            />
          </div>
          <div class="pt-4">
            <textarea
              id="about"
              v-model="customerAddress"
              rows="3"
              class="w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm rounded-md"
              placeholder="ที่อยู่"
            ></textarea>
          </div>
          <div class="pt-4">
            <BaseInput
              type="text"
              v-model="customerMobile"
              placeholder="เบอร์ติดต่อ"
            />
          </div>
          <div class="pt-4">
            <textarea
              name="about"
              v-model="customerDrugAllergy"
              rows="3"
              class="w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm rounded-md"
              placeholder="ข้อมูลการแพ้ยา"
            ></textarea>
          </div>
          <div class="pt-4">
            <AppButton color="bg-blue-400" text="เพิ่มข้อมูลสมาชิก" />
          </div>
        </form>
        <div class="pt-6">
          <AlertDialog
            v-if="showAlert"
            @closePressed="close"
            erroMessage="กรุณาตรวจสอบข้อมูลให้ครบถ้วน"
            color="bg-red-400"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from "../components/NavBar";
import { ref } from "@vue/reactivity";
import AppButton from "../components/Button";
import BaseInput from "../components/BaseInput.vue";
import AlertDialog from "../components/AlertDialog";
import router from "@/router";

export default {
  name: "Member",
  components: { NavBar, AppButton, BaseInput, AlertDialog },

  setup() {
    const customerName = ref("");
    const customerLastName = ref("");
    const customerAddress = ref("");
    const customerDrugAllergy = ref("");
    const customerMobile = ref("");
    const showAlert = ref(false);

    const handleSubmit = () => {
      console.log(customerName.value);
      console.log(customerLastName.value);
      if (customerName.value !== "" && customerLastName.value == "") {
        router.push({ name: "Home" });
      } else {
        showAlert.value = true;
      }
    };
    const close = () => {
      showAlert.value = false;
    };

    return {
      customerName,
      customerLastName,
      customerAddress,
      customerDrugAllergy,
      customerMobile,
      handleSubmit,
      showAlert,
      close,
    };
  },
};
</script>
